<template>
    <yd-layout class="secretaryDo chooseLeader">
        <yd-tab active-color="#333" :callback="callback">
            <yd-tab-panel label="送领导审批">
                <div class="mainContentFluid">
                    <div class="approval">
                        选择审批人
                    </div>
                    <div class="fluidWidth">
                        <div class="tripAppro">
                            <div class="name_circle">
                                <ul>
                                    <li>
                                        <div class="lastname" v-bind:class="{'check':leaderNum == 1}" v-on:click="leaderNum=1">
                                            廖
                                            <div class="nameClick">
                                            </div>
                                            <div class="imgClick">
                                            </div>
                                        </div>
                                        <div class="name">
                                            廖雯
                                        </div>
                                    </li>
                                </ul>
                                <div class="clear"></div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="reason_sec">
                    <div class="reasonTitle">填写附言</div>
                    <trsTextarea placeholder="附言最多为100汉字..." v-model="remarks"></trsTextarea>
                </div>
            </yd-tab-panel>
            <yd-tab-panel label="代领导录入">
                <div class="mainContentFluid">
                    <div class="approval">
                        常用意见
                    </div>
                    <div class="sugg_text">
                        <div class="oneText" :class="{'suggActive':suggNum==1}" @click="suggNum=1;commonAdvice='同意'">同意</div>
                        <div class="oneText twoText" :class="{'suggActive':suggNum==2}" @click="suggNum=2;commonAdvice='不同意'">不同意</div>
                        <div class="oneText threeText" :class="{'suggActive':suggNum==3}" @click="suggNum=3;commonAdvice='返回修改'">返回修改</div>
                    </div>
                </div>
                <div class="reason_sec">
                    <div class="reasonTitle">审批意见</div>
                    <trsTextarea placeholder="请输入您的意见" v-model="commonAdvice"></trsTextarea>
                </div>
            </yd-tab-panel>
        </yd-tab>
        <yd-tabbar slot="tabbar">
            <div class="agree">发送</div>
            <div class="back" @click="$router.go(-1)">返回</div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
// import { mapGetters } from 'vuex';
export default {
    name: 'secretaryDo',
    data() {
        return {
            tabKey: 0,
            leaderNum: 1,
            suggNum: 0,
            commonAdvice: '',
            remarks: ''
            // leaderName: ''
        }
    },
    mounted() {
        // let vm = this;
        document.setTitle('快捷审批流程');
    },
    computed: {
        // ...mapGetters(['getUserInfo', 'getSealInfo', 'getMishuInfo'])
    },
    methods: {
        callback(label, key) {
            let vm = this;
            vm.tabKey = key;
        }
    }
}
</script>
<style lang="less" scoped>
@import "proofofEmp.less";
.mainContentFluid {
     padding-bottom: .2rem;
}
.mainContentFluid,
.reason {
    border-top: .2rem solid #f8f8f8;
}

.submit {
    background-color: #a4ceff;
}

.sugg_text {
    height: .56rem;
    padding-left: .38rem;
    display: flex;
    justify-content: flex-start;
    margin-bottom: .4rem;
    .oneText {
        border: 1px solid #999;
        padding: 0 0.2rem;
        font-size: .32rem;
        color: #999;
        border-radius: 20px;
        line-height: .56rem;
        width: 1.3rem;
        text-align: center;
    }
    .twoText {
        width: 1.6rem;
        margin-left: .4rem;
    }
    .threeText {
        width: 2rem;
        margin-left: .4rem;
    }
}

.suggActive {
    background-color: #59a5fd;
    border: none!important;
    color: #fff!important;
}

.agree,
.back,
.otheragree {
    flex: 1;
    color: #fff;
    font-size: .36rem;
    line-height: 1rem;
    text-align: center;
}

.agree {
    background-color: #59a5fd;
}

.back {
    background-color: #a4ceff;
}

.disagree {
    background-color: #FF4677
}

.otheragree {
    background-color: #ffc000;
}
</style>


